<template lang="html">
  <div class="wrap">
    <headers></headers>
          <div class="title">
              <span class="titles">资费说明</span>
              /说明说明说明说明说明说明说明说明
          </div>
      <div class="container">
           <el-table
              :data="tableData"
              border
              style="width: 100%">
              <el-table-column
                prop="date"
                label="服务项目"
                width="180">
              </el-table-column>
              <el-table-column
                prop="name"
                label="收费标准"
                width="180">
              </el-table-column>
              <el-table-column
                prop="address"
                label="备注说明">
