            </el-table>

      </div><!--.container结束-->    
  </div>
</template>

<script>
import headers from "@/components/header"
export default {
  components:{
    headers
  },
  data(){
    return{
      tableData: [{
          date: '手机定位',
          name: '0.2元/次',
          address: '定位失败不扣费'
        }, {
          date: '手机短信',
          name: '0.1元/条',
          address: '发送失败不扣费'
        }, {
          date: '身份认证',
          name: '5元/次',
          address: '操作失败不扣费'
        }, {
          date: '代运保险',
          name: '费率千分之一',
          address: '平安运输损失险-普通货物-综合险资费标准'
        }]
    }
  }
}
</script>

<style lang="less">

.wrap{
   background:#fafbff;
   height:100%;
}
 .titles{
      font-size:16px;
      font-weight:700;
      color:#39509c;
     
    }
 
  .title{
    width:1200px;
    margin:0 auto;
    padding:28px 0 24px;
    font-size:12px;
    color:#8291b2;
    border-top:1px solid #edeef2;
     background:#fafbff;
  }
.container{
  border:1px solid #edeef2;
  padding:24px;
  width:1144px;
  
  margin:0 auto;
  background:white;
 
}


</style>


